// content script loaded in every web page that is loaded
// sends a message to background to see if URL is allowed

const querystring = require('querystring');


var globalWindow;

var browser;

if (!browser) {
    browser = chrome;
}

var checkForWindow = function() {    
    try {
        globalWindow = window;
    } catch (e) {
        if (e instanceof ReferenceError) {
            return false;
        }
        return true;
    }
    return true;
};

var pauseIfNotAllowed = function() {
    if (!checkForWindow()){
        return;
    }

    let message = {
        'subject': 'isUrlAllowed',
        'url': String(globalWindow.location.href),
    };

    browser.runtime.sendMessage(message, function(isUrlAllowed){
        if (isUrlAllowed == false) {
            
            let qString = querystring.stringify( { url: window.location.href});
            
            globalWindow.location.replace(browser.extension.getURL('html/pause.html')+"?"+qString);
        }
    });
};

pauseIfNotAllowed();

browser.runtime.onMessage.addListener(function (msg, sender, response) {
    if ((msg.subject === 'extensionActive')) {
        pauseIfNotAllowed();
    }
});
